import React from 'react';
import { useTable } from 'react-table';

export default function ColorTable(props) {
  const { data, openEditForm, deleteColorRecord } = props;

  const columns = React.useMemo(
    () => [
      {
        Header: 'Colour Name',
        accessor: 'Color_Name',
      },
      {
        Header: 'Colour Code',
        accessor: 'Color_Code',
        Cell: ({ row }) => (
            <>
                <div
                    style={{
                      backgroundColor: row.original.Color_Code,
                      width: '25px',
                      height: '25px',
                      borderRadius: '4px',
                      border: '1px solid #ccc',
                    }}
                  ></div>
            </>
          ),
      },

      {
        Header: 'Actions',
        accessor: 'action', // Dummy accessor
        Cell: ({ row }) => (
          <div>
            <i
              className="fa fa-edit pointer"
              onClick={() => openEditForm(row.original)}
              title="Edit"
            >
              {' '}
            </i>
            <i
              className="fa fa-trash ml-15 pointer"
              onClick={() => deleteColorRecord(row.original)}
              title="Delete"
            >
              {' '}
            </i>
          </div>
        ),
      },
    ],
    [openEditForm, deleteColorRecord]
  );

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({
    columns,
    data,
  });

  return (
    <div className="mt-4">
      <table {...getTableProps()} className="table">
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>{column.render('Header')}</th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => (
                  <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
